div {
  display: inline-block;
  margin-bottom: 20px;
}

h1, p {
  font-family: Lato;
}

form {
  border: 2px solid rgb(75, 74, 74); /* Граница вокруг формы */
  border-radius: 10px;
}

table { 
  width: 800px; /* Ширина таблицы */
  margin-left: 20px;
  border-collapse: collapse; /* Отображать только одинарные линии */
}

th { 
  text-align: center; /* Выравнивание по левому краю */
  background: #ccc; /* Цвет фона ячеек */
  padding: 5px;
  border: 2px solid rgb(150, 149, 149); /* Граница вокруг ячеек */
}

th#done {
  width: 90px;
}

th#priority {
  width: 110px;
}

th#date {
  width: 100px;
}

td { 
  padding: 5px; /* Поля вокруг содержимого ячеек */
  border: 2px solid rgb(150, 149, 149); /* Граница вокруг ячеек */
}

.hight {
  background-color: rgb(241, 135, 135);
}

.medium {
  background-color: rgb(247, 247, 139);
}

.low {
  background-color: rgb(144, 144, 247);
}

/*-----------------forms and table -----------------------*/

#root,
#root > div {
  width: 100%;
}

.form,
.filter {
  display: inline-block;
  width: 570px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 20px;
}

.table {
  margin-bottom: 20px;
  padding: 10px;
}

.table input[type="checkbox"] {
  width: 20px;
  height: 20px;
}

.form form,
.filter form {
  padding: 20px;
  background: rgb(230, 228, 228);
}

.form input,
.form textarea,
.filter input,
.filter textarea {
  margin: 5px 10px;
}

.form input[type='text'] {
  width: 90%;
}

.form button {
  width: 80px;
  height: 40px;
  margin: 5px 10px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  text-transform: uppercase;
  background-color: rgb(160, 247, 160);
  cursor: pointer;
}



